import { useQuery } from '@tanstack/react-query';
import { apiService } from '../services/api';
import type { DosenPengajar, GetLecturerAssignmentsParams } from '../types';

export function useLecturerAssignments(params?: GetLecturerAssignmentsParams) {
  return useQuery<DosenPengajar[], Error>({
    queryKey: ['lecturer-assignments', params],
    queryFn: () => apiService.getLecturerAssignments(params),
  });
}
